"use client";

import { useCart } from "@/context/CartContext";
import { Button } from "@/components/ui/button";
import { Minus, Plus, Trash2 } from "lucide-react";
import Image from "next/image";
import { useRouter } from "next/navigation";

export default function CartPage() {
  const router = useRouter();
  const { items, updateQuantity, removeFromCart, totalPrice } = useCart();

  if (items.length === 0) {
    return (
      <div className="container max-w-2xl mx-auto px-4 py-8 text-center">
        <h1 className="text-2xl font-bold text-gray-900 mb-4">Keranjang Belanja</h1>
        <p className="text-gray-500 mb-4">Keranjang belanja Anda masih kosong</p>
        <Button 
          className="bg-green-600 hover:bg-green-700 text-white"
          onClick={() => router.push("/")}
        >
          Mulai Belanja
        </Button>
      </div>
    );
  }

  return (
    <div className="container max-w-2xl mx-auto px-4 py-4 pb-24">
      <h1 className="text-2xl font-bold text-gray-900 mb-4">Keranjang Belanja</h1>

      <div className="space-y-4 mb-4">
        {items.map((item) => (
          <div 
            key={item.product.id} 
            className="flex gap-4 bg-white p-4 rounded-lg shadow-sm border border-gray-100"
          >
            <div className="relative w-24 h-24 flex-shrink-0">
              <Image
                src={item.product.image}
                alt={item.product.name}
                fill
                className="object-cover rounded-md"
              />
            </div>

            <div className="flex-1 min-w-0">
              <h3 className="font-medium text-gray-900">{item.product.name}</h3>
              <p className="text-green-600 font-bold">
                Rp {item.product.price.toLocaleString('id-ID')}
                <span className="text-gray-500 text-sm font-normal">/{item.product.unit}</span>
              </p>

              <div className="mt-2 flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <Button
                    variant="outline"
                    size="icon"
                    className="h-8 w-8"
                    onClick={() => updateQuantity(item.product.id, item.quantity - 1)}
                    disabled={item.quantity <= 1}
                  >
                    <Minus className="h-4 w-4" />
                  </Button>
                  <span className="w-8 text-center">{item.quantity}</span>
                  <Button
                    variant="outline"
                    size="icon"
                    className="h-8 w-8"
                    onClick={() => updateQuantity(item.product.id, item.quantity + 1)}
                    disabled={item.quantity >= item.product.stock}
                  >
                    <Plus className="h-4 w-4" />
                  </Button>
                </div>

                <Button
                  variant="ghost"
                  size="icon"
                  className="text-red-500 hover:text-red-600 hover:bg-red-50"
                  onClick={() => removeFromCart(item.product.id)}
                >
                  <Trash2 className="h-4 w-4" />
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Total and Checkout Button */}
      <div className="fixed bottom-16 left-0 right-0 bg-white border-t border-gray-200 p-4">
        <div className="container max-w-2xl mx-auto">
          <div className="flex items-center justify-between mb-4">
            <span className="text-gray-600">Total:</span>
            <span className="text-xl font-bold text-green-600">
              Rp {totalPrice.toLocaleString('id-ID')}
            </span>
          </div>
          <Button 
            className="w-full bg-green-600 hover:bg-green-700 text-white"
            onClick={() => router.push("/checkout")}
          >
            Lanjut ke Pembayaran
          </Button>
        </div>
      </div>
    </div>
  );
}
